import React from 'react';
import './Output.css';

export default function Output(props) {
  return (
    <div className="results-group">
      <label htmlFor={props.id}>
        {props.label}
        <span className="help-text">
        {props.helpText}
        </span>
      </label>
      <output name={props.id}>
        {props.value.toFixed(2)} {props.unit}
      </output>
    </div>
  );
}
